import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image"


export default function ProjectSingle({post}) {
    function capitalizeFirstLetter(str) {
        return str[0].toUpperCase() + str.slice(1);
    }

    return (
        <div className="main-wrapper relative pt-36 pb-20">
            <div className="container mx-auto mt-4 lg:mt-18">
                <Link to="/portfolio"> Return</Link>
                <div className="grid grid-cols-1 lg:grid-cols-5">
                    <div className="lg:col-span-2 flex items-center">
                        <div>
                            <div className="text-3xl font-bold">
                                {post.title}
                            </div>
                        </div>
                    </div>
                    <div className="lg:col-span-3 mt-12 lg:mt-0">
                        <Img className="object-cover h-full w-full" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt="Project image"/>
                    </div>
                </div>
                <div className="pt-4 pb-2 flex flex-row">
                    {post.tags.nodes.map((tag, i) => (
                        <span className="text-center text-white mr-5" key={i}>
                                                    <img src={`/img/develop_stack/${tag.name}.svg`} style={{height: "40px", margin: "10px auto"}} alt={tag.name} title={tag.name}/>
                            {capitalizeFirstLetter(tag.name)}
                                                </span>))}
                </div>

                <div className="mt-16" dangerouslySetInnerHTML={{__html: post.content}}/>
            </div>
        </div>
    )
}
