import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image";


export default function PartnerSingle({post}) {
    return (
        <div className="main-wrapper relative pt-36 pb-20">
            <div className="container mx-auto mt-4 lg:mt-18">
                <Link to="/our-partners"> Return</Link>
                <div className="grid grid-cols-1">
                    <div className="css--solutions-logo pr-10">
                        <Img objectFit="contain" className="h-full w-full" fixed={post.featuredImage.node.localFile.childImageSharp.fixed} alt="Partner image"/>
                    </div>
                    <div className="text-3xl font-bold">
                        {post.title}
                    </div>
                </div>
                <div className="mt-16" dangerouslySetInnerHTML={{__html: post.content}}/>
            </div>
        </div>
    )
}
