import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image";


export default function BlogSingle({post}) {
    return (
        <div className="main-wrapper relative pt-36 pb-20">
            <div className="container mx-auto mt-4 lg:mt-18">
                <Link to="/blogs"> Return</Link>
                <div className="grid grid-cols-1 lg:grid-cols-5">
                    <div className="lg:col-span-4 flex items-center">
                        <div>
                            <div className="text-3xl font-bold">
                                {post.title}
                            </div>
                        </div>
                    </div>
                    <div className="lg:col-span-1 mt-12 lg:mt-0">
                        <Img className="object-cover h-full w-full" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt="Blog image"/>
                    </div>
                </div>
                <div className="mt-16" dangerouslySetInnerHTML={{__html: post.content}}/>
            </div>
        </div>
    )
}
